@media screen and (max-width: 1024px) {

    input {
        padding: 22.5px 20px;
    }

    .input-row {
        display: block;
    }

    .input-box label {
        left: 21px;
        top: 24px;
    }
    
    input,
    label {
        font-size: 14px;
    }

    .column-logo {
        display: none;
    }

    .page-login .template-form {
        margin: 0 auto;
        padding: 0 20px;
        max-width: 500px;
    }

    .template-form {
        .btn-profile {
            position: absolute;
            left: 20px;
            top: 20px;
        }

        &__titles {
            min-height: 72px;
            font-size: 32px;
            padding: 11px 0 0 72px;
            display: flex;
            align-items: center;
        }
    
        &__subtitles {
            font-size: 18px;
            line-height: 1.5;
        }
    }

    .input-box {
        margin-bottom: 20px;
    }

    .control-box {
        margin: 44px 0;
    }

    .control-box label {
        font-size: 14px;
    }

    .template-form .btn-row {
        margin: 40px 0 20px;
    }

    .template-form__questions {
        font-size: 14px;
    }

    .btn {
        font-size: 16px;

        &.btn-lager{
            border-radius: 8px;
            padding: 21px 94px 23px;
        }
    }

    .hideShowPassword-toggle {
        width: 18px;
        height: 18px;
    }

    .nav {
        width: 50px;
        height: 50px;
        bottom: 20px;
        left: 20px;
        font-size: 14px;
    }
}